@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Unbounded:wght@500;600;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #e3e3e3;
}

button,
input {
  font-family: "Kanit", sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #111;
  color: #e3e3e3;
  display: flex;
  flex-direction: column;
  font-family: "Kanit", sans-serif;
  overflow-y: auto;
  overflow-x: hidden;
}

*::-webkit-scrollbar-button,
*::-webkit-scrollbar-corner,
*::-webkit-resizer {
  display: none;
}
*::-webkit-scrollbar {
  border-radius: 100%;
  width: 5px;
}
*::-webkit-scrollbar-button,
*::-webkit-scrollbar-corner,
*::-webkit-resizer {
  display: none;
}
*::-webkit-scrollbar-button,
*::-webkit-scrollbar-corner,
*::-webkit-resizer {
  display: none;
}
*::-webkit-scrollbar-thumb,
*::-webkit-scrollbar:horizontal {
  border-radius: 30px;
  background-color: #fff;
}
*::-webkit-scrollbar-track,
*::-webkit-scrollbar:horizontal {
  border-radius: 50px;
  background-color: #00000000;
}

.NavBox {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: 10px;
  display: flex;
  z-index: 99999;
}

nav {
  min-height: 65px;
  max-height: 65px;
  padding: 6px 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* background-color: #00000060;
  border: 1px #ffffff20 solid; */
  background-color: #b9b9b933;
  border: 1px #ffffff20 solid;
  backdrop-filter: blur(15px);
  border-radius: 25px;
  width: 100%;
}

.LogoName {
  font-family: Kanit;
  font-size: 24px;
  font-weight: 600;
  color: #fff;
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 18px;
}

nav ul li a {
  font-family: Kanit;
  font-size: 17px;
  font-weight: 500;
  color: #cecece;
  transition: 0.2s;
}

nav ul li a:hover {
  color: #ffffff;
}

.ActiveTab {
  color: #82ecff !important;
}

.ActiveTab:hover {
  color: #b2f3ff !important;
}

.Section1 {
  background: linear-gradient(145deg, #5c1efe, #111, #111, #111, #111, #00f0ff);
  padding: 115px 15px 115px 15px;
  display: flex;
  justify-content: space-evenly;
}

.Section1Div1 {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.Section1Div1 h1 {
  color: #fff;
  font-size: 65px;
  font-weight: 600;
  width: 500px;
  line-height: 60px;
}

.Section1Div1 p {
  color: #fff;
  font-family: Kanit;
  font-size: 45.238px;
  font-style: normal;
  font-weight: 400;
  width: 370px;
  line-height: 48px;
}

.Section1Div1BTNs {
  display: flex;
  align-items: center;
  gap: 15px;
}

.Section1DivBTN1 {
  padding: 7px 20px;
  font-size: 16px;
  background-color: #7041ea;
  color: #fff;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.2s;
}

.Section1DivBTN1:hover {
  background-color: #9973fa;
}

.Section1DivBTN2 {
  padding: 7px 20px;
  font-size: 16px;
  background: none;
  color: #fff;
  border: 1px #fff solid;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.2s;
}

.Section1DivBTN2:hover {
  color: #000;
  background: #fff;
}

.Section1Img1 {
  background-image: url("../Images/img1.png");
  background-size: cover;
  width: 270px;
  height: 305px;
}

/* Section 2 */

.Section2 {
  display: flex;
  flex-direction: column;
  background-color: #232323;
  padding: 90px 30px;
}

.Secition2Text {
  font-family: Kanit;
  font-size: 48px;
  font-weight: 600;
}

.Sec2Boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Sec2Box {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.Sec1BoxM {
  display: flex;
  gap: 25px;
  align-items: baseline;
}

.Sec1BoxM p {
  font-family: Kanit;
  font-size: 110.118px;
  font-weight: 600;
  font-style: normal;
  opacity: 0.15;
}

.Sec1BoxM span {
  display: flex;
  width: 20px;
  height: 20px;
  border-radius: 100%;
}

.Span1 {
  background-color: #7041ea;
}

.Span2 {
  background-color: #00f0ff;
}

.Span3 {
  background-color: #ff2df7;
}

.Sec2BoxName1 {
  font-family: Kanit;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  width: 280px;
}

.Sec2BoxName2 {
  font-size: 20px;
  font-weight: 400;
  width: 400px;
}

/* Big Section */

.BigSection {
  background: linear-gradient(145deg, #5c1efe, #111, #111, #111, #111, #00f0ff);
  padding: 95px 15px 115px 15px;
  display: flex;
  flex-direction: column;
  gap: 80px;
}

.MiniDiv1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.MiniDiv1 h1 {
  color: var(--ffffff, #fff);
  font-family: Kanit;
  font-size: 48px;
  font-weight: 600;
}

.MiniDivT1 {
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  width: 850px;
}

.MiniDivT2 {
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  width: 850px;
}

.MiniDiv2 {
  display: flex;
  flex-direction: column;
  gap: 25px;
  padding: 20px;
}

.MiniDiv2H1 {
  color: #fff;
  font-family: Kanit;
  font-size: 42px;
  font-weight: 600;
}

.MiniDiv2Boxes {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.MD2Box {
  width: 400px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  background-color: #ffffff23;
  backdrop-filter: blur(25px);
  border-radius: 25px;
  padding: 15px;
  cursor: default;
  transition: 0.2s !important;
}

.MD2Box:hover {
  background-color: #ffffff3d;
}

.MD2BoxIcon {
  font-size: 200%;
  width: max-content;
  padding: 2px 10px;
  border-radius: 15px;
}

.MD2BCC1 {
  box-shadow: inset 0 0 10px #00f0ff;
}

.MD2BCC2 {
  box-shadow: inset 0 0 10px #9973fa;
}

.MD2BCC3 {
  box-shadow: inset 0 0 10px #ff2df7;
}

.MD2BoxT1 {
  color: #fff;
  font-family: Kanit;
  font-size: 24px;
  font-weight: 600;
}

.MD2BoxT2 {
  font-size: 16px;
  font-weight: 400;
}

.MD2BoxMiniBTN {
  margin-top: 6%;
  font-size: 19px;
  text-align: center;
  color: #e2e2e2;
  cursor: pointer;
  transition: 0.2s;
}

.MD2BoxMiniBTN:hover {
  color: #ecfbff;
  transform: translateY(-3px);
}

/* Footer */

.FooterBox {
  padding: 20px;
}

footer {
  width: 100%;
  min-height: 50px;
  background-color: #232323;
  border-radius: 25px;
  padding: 40px 35px 35px 35px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.FooterSection1 {
  display: flex;
  justify-content: space-between;
}

.FooterDiv1 {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.FooterLogoLink {
  color: #e4e4e4;
  font-family: Kanit;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
}

.FooterLogoLink:hover {
  color: #b4fcff;
}

.FooterIcons {
  display: flex;
  align-items: center;
  gap: 12px;
}

.FooterIcons i {
  font-size: 20px;
  color: #e4e4e4;
  cursor: pointer;
  transition: 0.2s;
}

.FooterIcons i:hover {
  color: #b4fcff;
}

.FooterMiniLinks {
  display: flex;
  align-items: center;
  gap: 15px;
}

.FooterMiniLink {
  color: #f5f5f5;
  font-family: Kanit;
  font-size: 24px;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
  letter-spacing: -0.4px;
}

.FooterMiniLink:hover {
  color: #b4fcff;
}

.FooterPC {
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  text-align: right;
}

.FooterLineBox {
  width: 100%;
  display: flex;
  justify-content: center;
}

.FooterLine {
  width: 85%;
  border: 1px #fff solid;
  opacity: 0.2;
}

.FooterBottom {
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.BetaSection {
  background: linear-gradient(145deg, #5c1efe, #111, #111, #111, #111, #00f0ff);
  padding: 115px 15px 115px 15px;
  display: flex;
  height: 100vh;
  justify-content: space-evenly;
}

.BetaText {
  font-weight: 600;
  font-size: 50px;
}

/* Author : AroCodes */

.Author {
  text-align: center;
  font-family: "Unbounded", sans-serif;
  font-weight: 600;
  font-size: 28px;
  background-image: linear-gradient(145deg, #7afffb, #d65481);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.MiniInfo {
  text-align: center;
  font-family: "Unbounded", sans-serif;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 2px;
}
